<template>
  <v-app>
    <v-app-bar
      app
      color="#FBFBFB"
      flat
      dense
      outline
      style="color:#a7ad98"
      align="cetner"
    >
    WEDDING ANNOUNCEMENT
    <v-spacer></v-spacer>
      <span class="dday">
            D{{dday}}
      </span>
    </v-app-bar>

    <v-main style="background-color:#F2F2F2">
      <Announcement/>
    </v-main>
  </v-app>
</template>

<script>
import Announcement from './components/Announcement';

export default {
  name: 'App',

  components: {
    Announcement,
  },

  data: () => ({
    dday:"",
  }),
  created:function(){
    this.getDayGap()
  },
  methods:{
      getDayGap(){

      var dday = new Date("December 12, 2020 00:00:00").getTime();//디데이
      var nowday = new Date();//현재
      nowday = nowday.getTime();//밀리세컨드 단위변환
      var distance = dday - nowday;//디데이에서 현재까지 뺀다.

      var d = Math.floor(distance / (1000 * 60 * 60 * 24));//일
      console.log(d)
      if (d == 0) {//당일넘어섰을때, dday로 변경
          this.dday = "-day"
      } else if(d<0){
        this.dday = "+"+(d*-1)
      } else {
          this.dday= "-"+d+"";
      }
      }
  }
};
</script>
